<template>
  <div>
    <h1>我是home组件</h1>
    <div class="top-nav">
      <router-link class="nav-link" active-class="active" to="/home/news"
        >news</router-link
      >
      <router-link class="nav-link" active-class="active" to="/home/message"
        >message</router-link
      >
    </div>

    <!-- 将组件渲染出口写入 keep-alive组件中就可以保证组件不被销毁，include中的值表示不被销毁的组件名 -->
    <!-- include中的值指组件名   重要 -->
    <!-- <keep-alive include="News">
      <router-view></router-view>
    </keep-alive> -->

    <!-- 缓存多个写数组 -->
    <keep-alive :include="['News', 'Message']">
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: "Home",
};
</script>

<style lang="less" scoped>
.top-nav {
  height: 50px;
  background-color: grey;
  display: flex;
  margin-top: 15px;
  padding: 10px;
  .nav-link {
    background-color: #fff;
    border-radius: 5px;
    padding: 0 5px;
    text-decoration: none;
    line-height: 1.5rem;
    &:not(:first-child) {
      margin-left: 10px;
    }
  }

  .active {
    background-color: greenyellow;
    color: #fff;
  }
}
</style>