<template>
  <div>
    <ul>
      <li v-for="m in messsageList" :key="m.id">
        <!-- <router-link :to="`/home/message/detail/${m.id}/${m.title}`">
          {{ m.title }}
        </router-link> -->

        <!-- 如果使用params传参 则 只能写name不能写path -->
        <router-link
          :to="{
            name: 'detail',
            query: {
              id: m.id,
              title: m.title,
            },
          }"
        >
          {{ m.title }}
        </router-link>
        <button @click="pushShow(m)">push跳转</button>
        <button @click="replaceShow(m)">replace跳转</button>
      </li>
    </ul>
    <hr style="margin: 10px" />
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "Message",
  data() {
    return {
      messsageList: [
        {
          id: "001",
          title: "消息1",
        },
        {
          id: "002",
          title: "消息2",
        },
        {
          id: "003",
          title: "消息3",
        },
      ],
    };
  },
  methods: {
    pushShow(m) {
      this.$router.push({
        name: "detail",
        query: {
          id: m.id,
          title: m.title,
        },
      });
    },
    replaceShow(m) {
      this.$router.replace({
        name: "detail",
        query: {
          id: m.id,
          title: m.title,
        },
      });
    },
  },
  beforeDestroy() {
    console.log("Message组件被销毁");
  },
};
</script>

<style scoped>
li {
  margin-top: 10px;
}
</style>