<template>
  <div>
    <h1>我是about组件</h1>
  </div>
</template>

<script>
export default {
  name: "About",
  // 进入之前的守卫
  beforeRouteEnter(to, from, next) {
    if (to.meta.isAuth) {
      if (localStorage.getItem("school") == "atguigu") {
        next();
      } else {
        alert("权限不足！");
      }
    } else {
      next();
    }
  },
  // 离开之前的守卫
  beforeRouteLeave(to, from, next) {
    if (to.meta.isAuth) {
      if (localStorage.getItem("school") == "atguigu") {
        console.log("12312312");
        next();
      } else {
        alert("权限不足！");
      }
    } else {
      next();
    }
  },
};
</script>

