<template>
  <div>
    <h1>Vue Router Demo</h1>
    <button @click="back">后退</button>
    <button @click="forward">前进</button>
    <button @click="go">走指定的步数</button>
  </div>
</template>

<script>
export default {
  name: "Banner",
  methods: {
    back() {
      this.$router.back();
    },
    forward() {
      this.$router.forward();
    },
    go() {
      this.$router.go(3);
    },
  },
};
</script>

<style lang="less" scoped>
div {
  padding: 10px;
  background-color: greenyellow;
  h1 {
    text-align: center;
  }
  button {
    margin-right: 10px;
  }
}
</style>