<template>
  <ul>
    <li :style="{ opacity }">欢迎学习VUE</li>
    <li>news1 <input type="text" /></li>
    <li>news2 <input type="text" /></li>
    <li>news3 <input type="text" /></li>
    <li>news4 <input type="text" /></li>
  </ul>
</template>

<script>
export default {
  name: "News",
  data() {
    return {
      opacity: 1,
    };
  },
  // mounted() {
  //   setInterval(() => {
  //     this.opacity -= 0.01;
  //     if (this.opacity <= 0) this.opacity = 1;
  //   }, 16);
  // },
  // beforeDestroy() {
  //   console.log("News组件被销毁");
  // },

  // 当路由组件被激活是触发 (也就是路由组件显示时触发)
  activated() {
    this.timer = setInterval(() => {
      this.opacity -= 0.01;
      console.log("@");
      if (this.opacity <= 0) this.opacity = 1;
    }, 16);
  },
  // 当路由组件失活时触发 (路由组件被切换时触发)
  deactivated() {
    clearInterval(this.timer);
  },
};
</script>

<style scoped>
li {
  margin-top: 10px;
}
</style>